/* purgecss start ignore */

.lc-file-uploader {
  --tw-text-opacity: 1;
  color: rgba(var(--lc-color-gray-dark), var(--tw-text-opacity));
  margin-bottom: 24px;
}

.lc-file-uploader-disabled {
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    pointer-events: none;
    opacity: 0.6;
  }

.lc-file-uploader-dropzone {
    display: block;
    padding: 32px;
    text-align: center;
    border-style: solid;
    border-width: 1px;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    border-radius: 4px;
    border-color: rgb(var(--lc-color-gray-lighter));
    background-color: transparent;
  }

.lc-file-uploader-dropzone:hover {
      cursor: pointer;
      --tw-text-opacity: 1;
      color: rgba(var(--lc-color-primary), var(--tw-text-opacity));
      --tw-border-opacity: 1;
      border-color: rgba(var(--lc-color-primary), var(--tw-border-opacity));
      --tw-bg-opacity: 1;
      background-color: rgba(var(--lc-color-primary), var(--tw-bg-opacity));
      --tw-bg-opacity: 0.05;
    }

.lc-file-uploader-dropzone-active {
      border-style: dashed;
    }

.lc-file-uploader-dropzone-accept {
      color: rgb(var(--lc-color-primary)) !important;
      border-color: rgb(var(--lc-color-primary)) !important;
      background-color: rgb(var(--lc-color-primary), 0.05) !important;
    }

.lc-file-uploader-dropzone-reject {
      color: rgb(var(--lc-color-danger)) !important;
      border-color: rgb(var(--lc-color-danger)) !important;
      background-color: rgba(var(--lc-color-danger), 0.05) !important;
    }

.lc-file-uploader-dropzone-placeholder {
      display: inline-block;
      width: 64px;
    }

.lc-file-uploader-dropzone-text {
      font-size: 14px;
      line-height: 1.5;
      margin-top: 24px;
    }

.lc-file-uploader-dropzone-files {
      display: inline-flex;
      flex-wrap: wrap;
      list-style-type: none;
      margin: -12px;
      padding: 0;
      align-items: flex-start;
      justify-content: center;
    }

.lc-file-uploader-dropzone-files li {
        margin: 0;
        padding: 12px;
        flex: 1 1 16.666667%;
        min-width: 16.666667%;
        max-width: 240px;
      }

.lc-file-uploader-dropzone-file {
      transition-property: all;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
      transition-duration: 150ms;
      --tw-text-opacity: 1;
      color: rgba(var(--lc-color-primary), var(--tw-text-opacity));
      --tw-bg-opacity: 1;
      background-color: rgba(var(--lc-color-white), var(--tw-bg-opacity));
      padding: 4px;
      width: 100%;
      max-width: 100%;
    }

.lc-file-uploader-dropzone-file:hover {
        --tw-text-opacity: 1;
        color: rgba(var(--lc-color-primary-dark), var(--tw-text-opacity));
        --tw-bg-opacity: 1;
        background-color: rgba(var(--lc-color-white), var(--tw-bg-opacity));
      }

.lc-file-uploader-dropzone-file-thumb {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        --tw-bg-opacity: 1;
        background-color: rgba(var(--lc-color-gray-lightest), var(--tw-bg-opacity));
        width: 100%;
        border-radius: 4px;
      }

.lc-file-uploader-dropzone-file-thumb-image {
          display: block;
          max-width: 100%;
          height: 160px;
          object-fit: contain;
        }

.lc-file-uploader-dropzone-file-thumb-overlay {
          position: absolute;
          display: flex;
          align-items: center;
          justify-content: center;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          opacity: 0;
          transition-property: all;
          transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
          transition-duration: 150ms;
          border-radius: 4px;
          --tw-bg-opacity: 1;
          background-color: rgba(var(--lc-color-black), var(--tw-bg-opacity));
          --tw-bg-opacity: 0.4;
        }

.lc-file-uploader-dropzone-file:hover .lc-file-uploader-dropzone-file-thumb-overlay {
            opacity: 1;
          }

.lc-file-uploader-dropzone-file-remove-icon {
        --tw-text-opacity: 1;
        color: rgba(var(--lc-color-white), var(--tw-text-opacity));
      }

.lc-file-uploader-dropzone-file-name {
        font-size: 12px;
        line-height: 1.5;
        margin-top: 8px;
        word-break: break-all;
      }

.lc-file-uploader-errors {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-danger), var(--tw-text-opacity));
    margin: 0;
    margin-top: 16px;
    padding: 0;
    padding-left: 16px;
  }

.lc-file-uploader-errors li + li {
      margin-top: 8px;
    }

.lc-file-uploader-footer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding-top: 16px;
    margin: -8px;
  }

.lc-file-uploader-actions {
    display: flex;
    align-items: center;
    padding: 8px;
  }

.lc-file-uploader-restrictions {
    padding: 8px;
  }

/* purgecss end ignore */
